// export default Appointment;

import React, { useEffect, useState } from "react";
import {
  Card,
  CardContent,
  Typography,
  CircularProgress,
  Grid,
} from "@mui/material";
import "./Appointment.css"; // Ensure styling is applied

const Appointment = () => {
  const [appointments, setAppointments] = useState([]);
  const [loading, setLoading] = useState(false);
  const token = localStorage.getItem("token");

  useEffect(() => {
    if (!token) {
      setAppointments([]); // Clear when logged out
      return;
    }

    const fetchAppointments = async () => {
      setLoading(true);
      try {
        const response = await fetch("http://localhost:5000/api/appointments", {
          headers: { Authorization: `Bearer ${token}` },
        });

        if (response.ok) {
          const data = await response.json();
          setAppointments(data.appointments || []);
        } else {
          console.error("Failed to fetch appointments");
        }
      } catch (error) {
        console.error("Error fetching appointments:", error);
      }
      setLoading(false);
    };

    fetchAppointments();
  }, [token]);

  if (!token) {
    return (
      <Typography variant="h6" align="center" style={{ marginTop: 20 }}>
        Kindly login to see the appointments.
      </Typography>
    );
  }

  return (
    <div className="appointment-container">
      <Typography variant="h5" align="center" style={{ marginBottom: 20 }}>
        Your Appointments
      </Typography>

      {loading ? (
        <div style={{ textAlign: "center" }}>
          <CircularProgress />
        </div>
      ) : (
        <Grid container spacing={2} justifyContent="center">
          {appointments.length === 0 ? (
            <Typography variant="h6" align="center">
              No appointments found.
            </Typography>
          ) : (
            appointments.map((appointment) => (
              <Grid item xs={12} md={8} key={appointment._id}>
                <Card className="appointment-card">
                  <CardContent>
                    <Typography variant="h6">
                      Dr. {appointment.doctorName}
                    </Typography>
                    <Typography variant="body1">
                      <strong>Date:</strong> {new Date(appointment.date).toLocaleDateString()}
                    </Typography>
                    <Typography variant="body1">
                      <strong>Time:</strong> {appointment.time}
                    </Typography>
                    <Typography variant="body1">
                      <strong>Symptoms:</strong> {appointment.symptoms || "N/A"}
                    </Typography>
                    <Typography variant="body1">
                      <strong>Prior Medical History:</strong> {appointment.medicalHistory || "N/A"}
                    </Typography>
                  </CardContent>
                </Card>
              </Grid>
            ))
          )}
        </Grid>
      )}
    </div>
  );
};

export default Appointment;
