// Importing necessary modules and components
import React, { useState } from "react"; // React library and useState hook for managing state
import "./Header.css"; // Importing CSS file for styling the Header component
import logo from "../../assets/logo.jpeg"; // Importing the application logo image
import { Button, Modal, Tab, Tabs, Box } from "@mui/material"; 
// Importing Material-UI components: Button, Modal, Tab, Tabs, and Box for UI styling and layout

import Login from "../../screens/login/Login"; // Importing the Login component for user authentication
import Register from "../../screens/register/Register"; // Importing the Register component for user signup

// Header functional component
const Header = () => {
  // State to track whether the user is logged in (default is false)
  const [isLoggedIn, setIsLoggedIn] = useState(false);

  // State to control the visibility of the modal (open/close)
  const [openModal, setOpenModal] = useState(false);

  // State to manage the currently active tab in the modal (0 for Login, 1 for Register)
  const [activeTab, setActiveTab] = useState(0);

  // Function to open the modal
  const handleModalOpen = () => setOpenModal(true);

  // Function to close the modal
  const handleModalClose = () => setOpenModal(false);

  // Function to handle tab changes (switching between Login and Register tabs)
  const handleTabChange = (event, newValue) => setActiveTab(newValue);

  // Function to handle user logout
  const handleLogout = () => {
    setIsLoggedIn(false); // Set login state to false
    localStorage.removeItem("token"); // Remove the user's token from local storage for security
  };

  return (
    <header className="header">
      {/* Application logo displayed on the header */}
      <img src={logo} alt="App Logo" className="logo" />

      {/* Conditional rendering: Display Login button if the user is not logged in,
          otherwise display Logout button */}
      {!isLoggedIn ? (
        <Button variant="contained" color="primary" onClick={handleModalOpen}>
          Login
        </Button>
      ) : (
        <Button variant="contained" color="secondary" onClick={handleLogout}>
          Logout
        </Button>
      )}

      {/* Modal component for Login/Register functionality */}
      <Modal open={openModal} onClose={handleModalClose}>
        {/* Box component for modal content layout */}
        <Box className="modal-box">
          {/* Tabs for switching between Login and Register forms */}
          <Tabs
            value={activeTab} // Active tab index (Login or Register)
            onChange={handleTabChange} // Event handler for tab changes
            variant="fullWidth" // Ensures tabs take up equal space
          >
            {/* Tab for Login */}
            <Tab label="LOGIN" />
            {/* Tab for Register */}
            <Tab label="REGISTER" />
          </Tabs>

          {/* Conditional rendering of tab content */}
          <div className="tab-content">
            {/* Render Login component if activeTab is 0 */}
            {activeTab === 0 && (
              <Login 
                setIsLoggedIn={setIsLoggedIn} // Pass function to update login state
                onClose={handleModalClose} // Pass function to close the modal after login
              />
            )}
            {/* Render Register component if activeTab is 1 */}
            {activeTab === 1 && <Register />}
          </div>
        </Box>
      </Modal>
    </header>
  );
};

// Exporting the Header component for use in other parts of the application
export default Header;
