// export default Login;
/***************************************************************************************************************** */
import React, { useState, useEffect } from "react";
import { Button, FormControl, InputLabel, Input, Typography } from "@mui/material";
import { useNavigate, useLocation } from "react-router-dom"; // Navigation & Tracking

const Login = ({ setIsLoggedIn, onClose }) => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [errorMessage, setErrorMessage] = useState("");

  const navigate = useNavigate();
  const location = useLocation();
  const isLoggedIn = !!localStorage.getItem("token");

  useEffect(() => {
    // Store last visited page before login
    if (!isLoggedIn) {
      localStorage.setItem("previousPage", location.pathname);
    }
  }, [location, isLoggedIn]);

  const handleLogin = async () => {
    if (!email || !password) {
      setErrorMessage("All fields are required");
      return;
    }

    try {
      const response = await fetch("http://localhost:5000/api/users/login", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ email, password }),
      });

      const data = await response.json();

      if (response.ok && data.token) {
        localStorage.setItem("token", data.token);
        setIsLoggedIn(true);

        if (onClose) onClose();

        // Redirect to the last visited page
        const previousPage = localStorage.getItem("previousPage") || "/";
        navigate(previousPage);
      } else {
        setErrorMessage(data.error || "Invalid credentials");
      }
    } catch (error) {
      setErrorMessage("Failed to log in. Please check your network connection and try again.");
    }
  };

  const handleLogout = () => {
    localStorage.removeItem("token");
    setIsLoggedIn(false);

    // Redirect back to previous page or home
    const previousPage = localStorage.getItem("previousPage") || "/";
    navigate(previousPage);
  };

  return (
    <div>
      {isLoggedIn ? (
        <>
          <Typography variant="h6" gutterBottom>
            You are logged in
          </Typography>
          <Button variant="contained" color="secondary" fullWidth onClick={handleLogout}>
            Logout
          </Button>
        </>
      ) : (
        <>
          <Typography variant="h6" gutterBottom>
            Login
          </Typography>

          <FormControl fullWidth margin="normal">
            <InputLabel htmlFor="email">Email</InputLabel>
            <Input id="email" type="email" value={email} onChange={(e) => setEmail(e.target.value)} />
          </FormControl>

          <FormControl fullWidth margin="normal">
            <InputLabel htmlFor="password">Password</InputLabel>
            <Input id="password" type="password" value={password} onChange={(e) => setPassword(e.target.value)} />
          </FormControl>

          {errorMessage && <Typography color="error" variant="body2">{errorMessage}</Typography>}

          <Button variant="contained" color="primary" fullWidth onClick={handleLogin} style={{ marginTop: "20px" }}>
            Login
          </Button>
        </>
      )}
    </div>
  );
};

export default Login;
